
header {
  height: 100vh;
  background: #33aaff;
  padding: 20%;
  text-align: center;
  z-index: 9;
  position: fixed;
  width: 100vw;
  z-index: 1 !important;
}
header img {
    display: block;
    margin: auto;
}
section {
  z-index: 10 !important;
  position: relative;
  top: 100vh;
}
.dialog {
  background: #fff;
  border: 1px solid #ccc;
  box-shadow: 2px 2px 6px 0 #aaa;
  padding: .5rem;
  z-index: 101 !important;
}
.dialog.no-padding {
    padding: 0;
    border: none !important;
}
pre > code, code {
  font-size: 14px !important;
}
.link {
  cursor: pointer;
}
.button-menu {
  position: fixed !important;
  z-index: 100 !important;
  top: 30px;
  right: 30px;
}
.button-menu .button {
    border: none !important;
    box-shadow: 3px 3px 6px 0 #999 !important;
}
.button-menu .dialog {
    z-index: 101 !important;
}
.top-menu {
  position: relative !important;
  margin-top: -2px !important;
}
.empty-block {
  height: 100vh;
  display: block;
}
.footer {
  font-size: 16px !important;
  font-family: "Courier New", Courier, monospace;
}
.footer .new {
    font-size: 16px !important;
    color: #5aa5d8;
}
.footer .music {
    font-size: 16px !important;
    color: #c964aa;
    vertical-align: middle;
    display: inline-block;
    margin-top: -3px;
}



